<script>
export default {
  data () {
    return {
      isOpen: 0,
      submitting: false,
      collapses: [
        {
          title: ' ',
          text: ''
        },
        {
          title: ' ',
          text: ''
        },
        {
          title: ' ',
          text: ' dreamdepartmentstore2022@gmail.com '
        },
        {
          title: ' 1MB',
          text: '1MB'
        },
        {
          title: ' ',
          text: ' dreamdepartmentstore2022@gmail.com '
        }
      ],
      form: {
        nickname: '',
        account: '',
        question: '',
        content: '',
        images: []
      }
    }
  },
  computed: {
    productType () {
      return {
        images: this.form.images.length === 0 ? null : this.form.images.length >= 0 && this.form.images.length <= 5 ? 'is-success' : 'is-danger'
      }
    },
    productMessage () {
      return {
        images: this.form.images.length > 5 ? '' : ' 5  1MB'
      }
    }
  },
  methods: {
    deleteImage (index) {
      this.form.images.splice(index, 1)
    },
    async submit (event) {
      event.preventDefault()
      if (this.form.images.length > 5) {
        this.$swal({
          icon: 'error',
          title: '',
          text: ''
        })
        return
      }

      this.submitting = true
      const fd = new FormData()
      for (const key in this.form) {
        if (['images'].includes(key)) {
          for (const value of this.form[key]) {
            fd.append(key, value)
          }
        } else {
          fd.append(key, this.form[key])
        }
      }

      try {
        await this.api.post('/questions', fd, {
          headers: {
            authorization: 'Bearer ' + this.user.token
          }
        })
        this.$swal({
          icon: 'success',
          title: '',
          text: ''
        })
        this.form = {
          nickname: '',
          account: '',
          question: '',
          content: '',
          images: []
        }
      } catch (error) {
        this.$swal({
          icon: 'error',
          title: '',
          text: ''
        })
      }
      this.submitting = false
    }
  }
}
</script>
